import React, { Component } from "react";
import { Link } from "react-router-dom";
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavItem, MDBNavLink, MDBNavbarToggler, MDBCollapse } from "mdbreact";
import Media from 'react-media';
import styled, { keyframes } from 'styled-components';
import { tada } from 'react-animations';
import { rotateOutUpLeft } from 'react-animations';




const RotateOutUpLeftAnimation = keyframes`${rotateOutUpLeft}`;
const RotateOutUpLeftDiv = styled.div`
  animation: 1 1s ${RotateOutUpLeftAnimation};
`;

const TadaDiv = styled.div`animation: 2 1s ${keyframes`${tada}`}`;
// const bounceAnimation = keyframes`${bounce}`;

// const BouncyDiv = styled.div`
// animation: infinite 8s ${bounceAnimation};
// `;
class Navbar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isOpen: false
        };
    }
    toggleCollapse = () => {
        this.setState({ isOpen: !this.state.isOpen });
    }
    logOut = () => {
        sessionStorage.clear();
        this.props.handleLogOut();
    }
    // isLoggedIn = () => {
    //     if (this.props.isLoggedIn) {
    //         return (
    //             <MDBNavItem>
    //                 <MDBNavLink to="/" onClick={this.logOut}>
    //                     Log Out
    //                 </MDBNavLink>
    //             </MDBNavItem>
    //         )
    //     }
    // }

    render() {
        return (
            <div>
                <MDBNavbar color="#283593 indigo darken-3" dark expand="md">
                    <MDBNavbarBrand>
                        <TadaDiv>
                            <Media query="(max-width: 599px)">
                            {matches =>
                                matches ? (
                                    <Link className="navbar-brand containerFluid" to="/">
                                    <img className="containerFluid" src="https://i.ibb.co/yF7HS8y/bgwf-small.jpg" alt="Board Games with Friends" />
                                    </Link>
                                    ) : (
                                        <Link className="navbar-brand containerFluid" to="/">
                                        <img className="containerFluid" src="https://i.ibb.co/yXrwR6V/bgwf.jpg" alt="Board Games with Friends" />
                                        </Link>
                                        ) }
                                        </Media>
                        </TadaDiv>
                    </MDBNavbarBrand>
                    <MDBNavbarToggler onClick={this.toggleCollapse} />
                    <MDBCollapse id="navbarCollapse" isOpen={this.state.isOpen} navbar>
                        <MDBNavbarNav right>
                        {!this.props.isLoggedIn &&
                            <MDBNavItem active>
                                <MDBNavLink to="/"><i className="fas fa-home"></i>
                                    Login
                                </MDBNavLink>
                            </MDBNavItem>
                        }
                            <MDBNavItem>
                                <MDBNavLink to="/dashboard">
                                    <RotateOutUpLeftDiv>
                                        <i className="fas fa-chess-rook"></i>
                                        Events
                                    </RotateOutUpLeftDiv>
                                </MDBNavLink>
                            </MDBNavItem>
                            <MDBNavItem>
                                <MDBNavLink to="/find-game">
                                    <i className="fas fa-chess-knight"></i>
                                    Game Catalog
                                </MDBNavLink>
                            </MDBNavItem>
                            <MDBNavItem>
                                <MDBNavLink to="/news">
                                    <i className="far fa-newspaper"></i>
                                    News
                                 </MDBNavLink>
                            </MDBNavItem>
                            {this.props.isLoggedIn &&
                                <MDBNavItem>
                                    <MDBNavLink to="/profile">
                                        <i className="far fa-address-card"></i>
                                        Profile
                                    </MDBNavLink>
                                </MDBNavItem>
                            }
                            {this.props.isLoggedIn &&
                                <MDBNavItem>
                                    <MDBNavLink to="/" onClick={this.logOut}>
                                        Log Out
                                    </MDBNavLink>
                                </MDBNavItem>
                            } 
                        
                        </MDBNavbarNav>
                    </MDBCollapse>
                </MDBNavbar>
            </div>
        );
    }
}

export default Navbar;